body {
  margin: 0;
  font-family: "Poppins", sans-serif;
}

.header {
  background-image: url("/assets/images/header.jpg");

  // mobile
  @media (max-width: 768px) {
    height: 330px;
    background-size: cover;
    background-position: center;
  }
  // DESKTOP
  @media (min-width: 768px) {
    height: 100vh;
    background-size: cover;
  }
}

.hamburger {
  // mobile
  @media (max-width: 768px) {
  }
  // DESKTOP
  @media (min-width: 768px) {
    display: none;
  }
}

.links {
  display: none;
  //tablet
  @media (min-width: 768px) {
    display: block;
  }
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 61px;
}

.lema {
  padding-top: 41px;
  margin-bottom: 41px;
  text-align: center;
  color: #0c474b;
  font-size: 20pt;
}

.section1 {
  background-color: #eee8e1;
  padding-bottom: 61px;
}

.section1green {
  color: #0c474b;
}
.li {
  list-style: none;
  color: #023538;
  font-size: 15pt;
}
.section2 {
  background-color: #ff975a;
  color: #eee8e1;
  height: 352px;
}

.section3 {
  background-color: #eee8e1;
  color: #0c474b;
}

.footer {
  background-color: #03565a;
  color: #eee8e1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.footer-ul {
  list-style: none;
}
.links-footer {
  text-decoration: none;
  color: #eee8e1;
}
